.jsoneditor-container {
  width: 100%;
  height: 100%;
  position: relative;
}

div.jsoneditor {
  border: 1px solid #ddd;
  border-radius: 4px;
  box-shadow: 0 2px 6px #0000000d;
}

div.jsoneditor-menu {
  background-color: #f8f9fa;
  border-bottom: 1px solid #ddd;
}

div.jsoneditor-tree {
  font-family: Roboto Mono, monospace;
}

div.jsoneditor-field {
  color: #06c;
  font-weight: 500;
}

div.jsoneditor-value {
  color: #333;
}

.jsoneditor-value.jsoneditor-string {
  color: green;
}

.jsoneditor-value.jsoneditor-number {
  color: #00f;
}

.jsoneditor-value.jsoneditor-boolean {
  color: #ff8000;
}

.jsoneditor-value.jsoneditor-null {
  color: #999;
}

mark.jsoneditor-highlight {
  color: inherit;
  background-color: #ffd70080;
  border-radius: 2px;
}

div.jsoneditor-tree button.jsoneditor-button:hover {
  background-color: #0066cc1a;
}

#jsonModal .modal-content {
  border-radius: 6px;
  box-shadow: 0 5px 15px #0003;
}

#jsonModal .modal-header {
  background-color: #f8f9fa;
  border-bottom: 1px solid #eee;
  padding: 15px 20px;
}

#jsonModal .modal-body {
  padding: 0;
}

#jsonModal .modal-footer {
  background-color: #f8f9fa;
  border-top: 1px solid #eee;
  padding: 15px 20px;
}

#jsonModal .btn-primary {
  background-color: #0d6efd;
  border-color: #0d6efd;
  transition: all .2s;
}

#jsonModal .btn-primary:hover {
  background-color: #0b5ed7;
  border-color: #0a58ca;
}

.editor-mode-selector {
  background-color: #f8f9fa;
  border-radius: 4px;
  padding: .25rem;
}

.editor-mode-btn {
  padding: .25rem .5rem;
  font-size: .875rem;
}

.editor-mode-btn.active {
  color: #fff;
  background-color: #0d6efd;
  border-color: #0d6efd;
}

#jsoneditor {
  height: 500px;
}

@media (height >= 800px) {
  #jsoneditor {
    height: 600px;
  }
}

.jsoneditor-menu:after {
  content: "Ctrl+F to search";
  color: #777;
  font-size: 11px;
  position: absolute;
  top: 8px;
  right: 10px;
}

.modal.fade .modal-dialog {
  opacity: 0;
  transition: transform .2s, opacity .2s;
  transform: scale(.95);
}

.modal.fade.show .modal-dialog {
  opacity: 1;
  transform: scale(1);
}
